  import {mapActions} from 'vuex';



  export default {
  name: 'Record',
    props:['editId'],
  data () {
    return {
      test:'',
      item_in:'',
      formData :
        {
          in : '',
          out : '',
          businessId : this.editId,
        }

    }
  },

  methods:{
    ...mapActions('LedgerStore',['addRecord']),
    onSubmit(){
      this.addRecord(this.formData);

    }

  }
}
